<template>
<div>
    <v-app-bar color="white" app>
      <v-toolbar-title color="grey"  class="pl-4 ml-0" dark>
        <b>Account</b>
      </v-toolbar-title>
      <v-spacer />
      <v-avatar size="35" color="grey" >
        <span>{{$store.getters.GetUserInfo ? $store.getters.GetUserInfo.username[0]  : '' | uppercase}}</span>
      </v-avatar>
    </v-app-bar>
  <h1   class="text-center pt-2">Welcome, {{  $store.getters.GetUserInfo ? $store.getters.GetUserInfo.username : '' }}</h1>
</div>
</template>

<script>
import { UtilityMixin } from "../mixins/utility";

export default {
    mixins:[UtilityMixin],
  data() {
    return {
      
    };
  },
  mounted() {
    this.GetUserInfo();
  },
  methods: {
    GetUserInfo() {
      this.$store.dispatch("GET_USER_API", "").then((data) => {
        this.UserInfo = data.data;
      });
    },
  },
};
</script>

